import React from "react";
import './Menu.css';
import logo from '../assets/imgs/logo.png';
import user_icon from '../assets/imgs/user.svg';
import logo_min from '../assets/imgs/logo_min.png';
import {NavLink} from "react-router-dom";


const Menu = (props) => {

    const sublinks = React.createRef();
    const sSublinks = React.createRef();
    const menuBlock = React.createRef();
    const menu = React.createRef();

    const makeActive = () => {
        let windowWidth = window.innerWidth;
        if (windowWidth >= 768) {
            sublinks.current.classList.add('active');
            sSublinks.current.classList.remove('active');
        }
    }
    const makeActiveOnHover = () => {
        let windowWidth = window.innerWidth;
        if (windowWidth >= 768) {
            sublinks.current.classList.add('active');
            sSublinks.current.classList.remove('active');
        }
        if (windowWidth < 768) {
            menu.current.classList.add('active');
            menuBlock.current.classList.add('active');
            sublinks.current.classList.add('active__mobile');
            sSublinks.current.classList.remove('active__mobile');
        }
    }

    const makeActiveChild = () => {
        let windowWidth = window.innerWidth;
        if (windowWidth >= 768) {
            sSublinks.current.classList.add('active');
        }
    }
    const makeActiveChildOnHover = () => {
        let windowWidth = window.innerWidth;
        if (windowWidth >= 768) {
            sSublinks.current.classList.add('active');
        }
        if (windowWidth < 768) {
            menu.current.classList.add('active');
            sSublinks.current.classList.add('active__mobile');
        }
    }
    const makeInactive = () => {
        let windowWidth = window.innerWidth;
        sublinks.current.classList.remove('active');
        if (windowWidth < 768) {
            sublinks.current.classList.remove('active__mobile');
            sSublinks.current.classList.remove('active__mobile');
        }
    }
    const makeInactiveOnHover = () => {
        let windowWidth = window.innerWidth;
        if (windowWidth < 768) {
            menu.current.classList.remove('active');
            menuBlock.current.classList.remove('active');
            sublinks.current.classList.remove('active__mobile');
        }
    }
    const makeInactiveChildOnHover = () => {
        let windowWidth = window.innerWidth;
        if (windowWidth < 768) {
            sSublinks.current.classList.remove('active__mobile');
        }
    }

    return (
        <div className='menu' ref={menu}>
            <div className="user__block">
                <img src={logo} className="logo"/>
                <img src={logo_min} className='logo__min'/>
                <div className="menu__user__block">
                    <NavLink to='/profile' className="user__icon__block" onClick={makeInactive}>
                        <img src={user_icon} className="user__icon"/>
                    </NavLink>
                    <div className="menu__user__info__block">
                        <p className="user__name">Vishal Bhatia</p>
                        <p className="user__email">koolguy@gmail.com</p>
                    </div>
                </div>
            </div>
            <div className="menu__block" ref={menuBlock}>
                <NavLink to='/dashboard' activeClassName={'active'} className="menu__link" onClick={makeInactive}>
                    <span className="icon-menu_icon_1 link__icon"></span>
                    <span className="link__title">Dashboard</span>
                </NavLink>
                <NavLink to='/businesses' className="menu__link" onClick={makeActive}
                                                                    onMouseEnter={makeActiveOnHover}
                                                                    onMouseLeave={makeInactiveOnHover}>
                    <span className="icon-menu_icon_2 link__icon"></span>
                    <span className="link__title">Businesses</span>
                </NavLink>
                <div className="sublinks__block" ref={sublinks} onMouseEnter={makeActiveOnHover}>
                    <NavLink to='/create_business' className="menu__link sublink">
                        <span className='link__icon plus__icon'>+</span>
                        <span className="link__title">Create a Business</span>
                    </NavLink>
                    <NavLink to='/business_profile' className="menu__link sublink" onClick={makeActiveChild}
                                                                                    onMouseEnter={makeActiveChildOnHover}>
                        <span className="icon-menu_icon_2 link__icon sublink__icon"></span>
                        <span className="link__title">Kool Company</span>
                    </NavLink>
                    <div className="sublinks__block s__sublinks__block" ref={sSublinks} onMouseEnter={makeActiveChildOnHover}
                                                                                        onMouseLeave={makeInactiveChildOnHover}>
                        <NavLink to='/business/payees' className="menu__link sublink" onClick={makeInactive}>
                            <span className="icon-menu_icon_5 link__icon sublink__icon"></span>
                            <span className="link__title">Payees</span>
                        </NavLink>
                        <NavLink to='/business/members' className="menu__link sublink" onClick={makeInactive}>
                            <span className="icon-menu_icon_6 link__icon sublink__icon"></span>
                            <span className="link__title">Members</span>
                        </NavLink>
                    </div>
                </div>
                <div className="menu__border__div"></div>
                <NavLink to='/logout' className="menu__link" onClick={makeInactive}>
                    <span className="icon-menu_icon_4 link__icon"></span>
                    <span className="link__title">Logout</span>
                </NavLink>
            </div>
        </div>
    )
}

export default Menu;